ro General Public License\n - along with this program. If not, see <http://www.gnu.org/licenses/>.\n -\n -->\n\n<template>\n\t<DashboardWidget :items=\"tasks\" :loading=\"loading\" :show-more-url=\"''\">\n\t\t<template #default=\"{ item }\">\n\t\t\t<DashboardWidgetItem :target-url=\"getItemUrl(item.id)\"\n\t\t\t\t:avatar-url=\"getAvatarUrl()\"\n\t\t\t\t:avatar-username=\"getAvatarUsername()\"\n\t\t\t\t:avatar-is-no-user=\"true\"\n\t\t\t\t:main-text=\"getItemText(item)\"\n\t\t\t\t:sub-text=\"`${item.owner} - ${getStatusBadge(item)}`\" />\n\t\t</template>\n\t\t<template #empty-content>\n\t\t\t<div class=\"empty-tasks-list\">\n\t\t\t\t<EmptyContent style=\"margin-top: 5vh;\">\n\t\t\t\t\t{{ t('mediadc', 'No tasks yet') }}\n\t\t\t\t\t<template #icon>\n\t\t\t\t\t\t<img class=\"empty-tasks-list-icon\" style=\"width: 100%; height: auto;\" src=\"data:image/svg+xml;base64,\">\n\t\t\t\t\t\t<img class=\"empty-tasks-list-icon--dark\" style=\"width: 100%; height: auto;\" src=\"data:image/svg+xml;base64,\">\n\t\t\t\t\t</template>\n\t\t\t\t\t<template #desc>\n\t\t\t\t\t\t<a :href=\"getAppUrl()\">{{ t('mediadc', 'Create a new one!') }}</a>\n\t\t\t\t\t</template>\n\t\t\t\t</EmptyContent>\n\t\t\t</div>\n\t\t</template>\n\t</DashboardWidget>\n</template>\n\n<script>\nimport { getCurrentUser } from '@nextcloud/auth'\nimport { generateUrl } from '@nextcloud/router'\nimport { loadState } from '@nextcloud/initial-state'\nimport { DashboardWidget, DashboardWidgetItem } from '@nextcloud/vue-dashboard'\n\nimport Formats from '../mixins/Formats.js'\n\nimport EmptyContent from '@nextcloud/vue/dist/Components/EmptyContent.js'\n\nconst tasks = loadState('mediadc', 'mediadc-recent-tasks')\n\nexport default {\n\tname: 'Dashboard',\n\tcomponents: {\n\t\tDashboardWidget,\n\t\tDashboardWidgetItem,\n\t\tEmptyContent,\n\t},\n\tmixins: [\n\t\tFormats,\n\t],\n\tdata() {\n\t\treturn {\n\t\t\ttasks,\n\t\t\tloading: true,\n\t\t}\n\t},\n\tasync mounted() {\n\t\tthis.loading = false\n\t},\n\tmethods: {\n\t\tgetAppUrl() {\n\t\t\treturn generateUrl('/apps/mediadc')\n\t\t},\n\t\tgetItemUrl(taskId) {\n\t\t\treturn generateUrl(`/apps/mediadc/tasks/${taskId}`)\n\t\t},\n\t\tgetAvatarUrl() {\n\t\t\treturn generateUrl(`/avatar/${getCurrentUser().uid}/32`)\n\t\t},\n\t\tgetAvatarUsername() {\n\t\t\treturn getCurrentUser().uid\n\t\t},\n\t\tgetItemText(item) {\n\t\t\treturn this.parseTargetMtype(item) + ' ' + item.files_total + ' ' + this.n('mediadc', 'file', 'files', Number(item.files_total)) + ' (' + this.formatBytes(item.files_total_size) + ')'\n\t\t},\n\t},\n}\n</script>\n\n<style scoped>\n.empty-tasks-list {\n\tpadding: 20px;\n\twidth: 100%;\n\theight: 80%;\n\tdisplay: flex;\n\talign-items: center;\n\tjustify-content: center;\n\tbox-sizing: border-box;\n\tflex-direction: column;\n}\n\n.empty-tasks-list a {\n\ttext-decoration: underline;\n}\n\n.empty-tasks-list-icon, .empty-tasks-list-icon--dark {\n\twidth: 96px;\n\theight: 96px;\n}\n\n.empty-tasks-list-icon--dark {\n\tdisplay: none;\n\tvisibility: hidden;\n}\n\nbody.theme--dark .empty-tasks-list-icon--dark {\n\tdisplay: block;\n\tvisibility: visible;\n\topacity: 0.5;\n}\n\nbody.theme--dark .empty-tasks-list-icon {\n\tdisplay: none;\n\tvisibility: hidden;\n}\n</style>\n"],"sourceRoot":""}]);
